import * as types from "./mutation-types";

const mutations = {
  [types.SET_CUSTOMERAREA] (state, area) {
    state.customerArea = area;
  },
  [types.SET_CUSTOMERADDRESS] (state, address) {
    state.customerAddress = address;
  },
  [types.SET_CUSTOMERGEOADDRESS] (state, address) {
    state.customerGeoAddress = address;
  },
  [types.SET_BACKPATH] (state, path) {
    state.backPath = path;
  },
  setTeamCustomerID (state, id) {
    state.teamCustomerID = id;
  },
  setTeamUserIDArr (state, arr) {
    state.teamUserIDArr = arr;
  },
  setDictData (state, data) {
    state.dict[data.type] = data.data;
  },
  clearCust (state) {
    state.customerID = 0;
    state.customerName = '';
    state.linkUserID = 0;
  },
  setChooseProduct (state, data) {
    state.chooseProductData = data
  },
  ['CHANGEORGID'] (state, orgID) {
    state.orgID = orgID
  },
  ['CLEARORGID'] (state) {
    state.orgID = null
  },
  setChooseCustomer (state, data) {
    state.chooseCustomer = data
  },
  setChooseLinkMan (state, data) {
    state.chooseLinkMan = data
  },
  setIsMyClient (state, isB) {
    state.isMyClient = isB
  },
  setClientTab (state, index) {
    state.clientTab = index
  },
  setCurrentUserID (state, ID) {
    state.CurrentUserID = ID
  },
  setOrgID (state, ID) {
    state.orgID = ID
  },
  setAuth (state, data) {
    state.auth = data
  },
  setAdmin (state, admin) {
    state.isAdmin = admin
  },
  setSelectCustomer (state, val) {
    state.selectedCustomer.ID = val.ID;
    state.selectedCustomer.CustomerName = val.CustomerName;
  },
  setCurrentComp (state, val) {
    state.CurrentComp = val;
  },
  setToken (state, val) {
    state.token = val
  }
};

export default mutations;



