import api from "./common/test"
api(isTest)


let user_str = localStorage.getItem("user")
if (user_str) {
let user = JSON.parse(user_str)
store.commit("setCurrentUserID", user.data.UserID)
store.commit("setCurrentComp", user.data.CompMdt)
store.commit("setOrgID", user.data.OrgID)
axios.defaults.headers.Authorization = "Bearer " + user.data.Token
store.commit('setToken', user.data.Token)
}


Vue.prototype.BillFileUrl = type =>
`${api47}/api/v1/BillFile/FileUpload?fileType=${type}`
Vue.prototype.Axios = axios

Vue.prototype.getAxios = (url, params = {}) =>
axios
.get(url, {
params
})
.then(res => {
if (res.data.State === 1) {
return Promise.resolve(res.data)
} else {
return Promise.reject(res.data)
}
})
Vue.prototype.postAxios = (url, data) =>
axios.post(url, data).then(res => {
if (res.data.State === 1) {
return Promise.resolve(res.data)
} else {
return Promise.reject(res.data)
}
})

Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
el: "#app",
router,
store,
components: {
App
},
template: "<App/>"
})



