//我的客户
const client = () => import("views/client/client")
//公共客户
const comClient = () => import("views/client/commonClient")
//共享客户
const shareClient = () => import('views/client/shareClient')
//客户详情
const clientDetails = () => import("views/client/clientDetails")
//联系人详情
const userDetails = () => import('views/client/userDetails')
//新增联系人
const addCustUser = () => import("views/client/addCustUser")
//新增跟进
const addFollow = () => import("views/follow/addFollow")
//跟进详情
const followDetails = () => import("views/follow/followDetails")
//新增客户
const addClient = () => import("views/client/AddClient")
const addressMap = () => import("views/client/addressMap")
const addCustomerSelectPage = () => import("views/client/addressSeletePage")
//新增任务
const addTask = () => import("components/AddTask/template")
//新增订单
const newOrder = () => import("views/order/NewOrder");
const selectCust = () => import("views/client/SelectCust")
const chooseProduct = () => import("components/order/chooseProduct")
//订单详情
const orderInfo = () => import("views/order/OrderInfo")
const changeOrder = () => import('views/order/ChangeOrder')
//回款
const addReceive = () => import("views/receive/AddReceive")
//收支
const newCost = () => import("components/cost/newCost")
// 任务详情
const TaskInfo = () => import("views/TaskInfo/template")
// 收支详情
const costInfo = () => import("views/cost/costInfo")
//
//
//
//
//
//
//
//
//
//
//

const details = [{
  path: "clientDetails",
  component: clientDetails,
  children: [
    //跟进
    {
      path: "addFollow",
      component: addFollow,
      children: [
        {
          path: "selectCust",
          component: selectCust
        }
      ]
    },
    {
      path: 'followDetails/:id',
      component: followDetails,
      props: true
    },
    //客户
    {
      path: "addClient",
      component: addClient,
      children: [
        {
          path: "addressMap",
          component: addressMap
        },
        {
          path: "addCustomerSelectPage",
          component: addCustomerSelectPage
        }
      ]
    },
    //联系人
    {
      path: "userDetails/:id",
      component: userDetails,
      props: true
    },
    {
      path: "addCustUser",
      component: addCustUser
    },
    //任务
    {
      path: "addTask",
      component: addTask
    },
    //任务详情
    {
      path: "TaskInfo",
      component: TaskInfo
    },
    //订单
    {
      path: "addOrder",
      component: newOrder,
      children: [
        {
          path: "selectCust",
          component: selectCust
        },
        {
          path: "chooseProduct",
          component: chooseProduct
        },
      ]
    },
    {
      path: "orderInfo",
      component: orderInfo,
      children: [
        {
          path: 'changeOrder',
          meta: {
            refresh: false,
          },
          component: changeOrder,
          children: [
            {
              path: "selectCust",
              component: selectCust
            },
            {
              path: "chooseProduct",
              component: chooseProduct
            },
          ]
        }
      ]
    },
    //收支
    {
      path: "newCost",
      component: newCost,
      children: [
        {
          path: "selectCust",
          component: selectCust
        },
      ]
    },
    {
      path: "reimbursement",
      component: () => import('views/cost/reimbursement'),
      children: [
        {
          path: "selectCust",
          component: selectCust
        },
      ]
    },
    {
      path: "costInfo/:id",
      component: costInfo,
    },
    //回款
    {
      path: "receive",
      component: () => import("views/receive/ReceiveList"),
    },
    {
      path: "addReceive",
      component: addReceive,
      children: [
        {
          path: "selectOrder",
          component: () => import("views/receive/orderSelector")
        },
        {
          path: "selectCust",
          component: selectCust
        }
      ]
    },
    {
      path: '/receiveInfo',
      component: () => import('views/receive/ReceiveInfo'),
    },
    {
      path: '/changeReceive',
      meta: {
        refresh: false,
      },
      component: addReceive,
      children: [
        {
          path: "selectCust",
          component: selectCust
        },
        {
          path: "selectOrder",
          component: () => import("views/receive/orderSelector.vue")
        }
      ]
    },
  ]
}]

const clientRouter = [ //我的客户
  {
    path: "/client",
    component: client,
    meta: {
      isClient: true,
    },
    children: details
  },
  //公共客户
  {
    path: "/commonClient",
    component: comClient,
    meta: {
      isClient: true,
    },
    children: details
  },
  //共享客户
  {
    path: '/shareClient',
    component: shareClient,
    meta: {
      isClient: true,
    },
    children: details
  },]

export default clientRouter



