import Vue from "vue";
import Router from "vue-router";
import client from "./client"


const index = () => import("views/index/index");
// //我的客户
// const client = () => import("views/client/client")
// //公共客户
// const comClient = () => import("views/client/commonClient")
// //共享客户
// const shareClient = () => import('views/client/shareClient')
//客户详情
const clientDetails = () => import("views/client/clientDetails")
//联系人详情
const userDetails = () => import('views/client/userDetails')
//新增联系人
const addCustUser = () => import("views/client/addCustUser")
//联系跟进
const follow = () => import("views/follow/followList")
//跟进详情
const followDetails = () => import("views/follow/followDetails")
//新增跟进
const addFollow = () => import("views/follow/addFollow")

// 添加客户及子页面
const addressMap = () => import("views/client/addressMap");

// 订单相关
const newOrder = () => import("views/order/NewOrder");
//收支详情
const costInfo = () => import('views/cost/costInfo')
const newCost = () => import('components/cost/newCost')

Vue.use(Router);

export default new Router({
  routes: [{
      path: "/",
      name: "index",
      component: index
    },
    ...client,
    //客户详情
    {
      path: "/clientDetails",
      component: clientDetails,
    },
    //人员详情
    {
      path: "/userDetails/:id",
      name: 'userDetails',
      component: userDetails,
      props: true
    },
    //新增客户
    {
      path: "/addClient",
      component: () => import("views/client/AddClient"),
      children: [{
        path: "selectDict",
        component: () => import("components/dict/DictSelectorPage")
      },
      {
        path: "selectUser",
        component: () => import("components/userSelector")
      },
      {
        path: "addCustomerSelectPage",
        component: () => import("views/client/addressSeletePage")
      },
      {
        path: "addressMap",
        component: addressMap
      },
      {
        path: "selectFrom",
        component: () => import("views/client/OtherSelectorPage")
      }]
    },
    //新增客户联系人
    {
      path: "/addCustUser",
      component: addCustUser
    },
    {
      path: "/TaskWork",
      name: "TaskWork",
      component: () => import("views/TaskWork/template"),
      meta: {
        keepAlive: true
      }
    },
    //联系跟进
    {
      path: "/followUp",
      component: follow,
      children: [{
        path: 'followDetails/:id',
        component: followDetails,
        props: true
      },
      {
        path: "addFollow",
        component: addFollow,
        children: [{
          path: "selectCust",
          component: () => import("views/client/SelectCust")
        }]
      }, ]
    },
    //跟进详情
    {
      path: '/followDetails/:id',
      component: followDetails,
      props: true
    },
    //新增跟进
    {
      path: "/addFollow",
      component: addFollow,
      children: [{
        path: "selectCust",
        component: () => import("views/client/SelectCust")
      }]
    },
    {
      path: "/selectCust",
      component: () => import("views/client/SelectCust")
    },
    {
      path: "/AddTask",
      name: "AddTask",
      component: () => import("components/AddTask/template"),
      children: [{
        path: "selectCust",
        component: () => import("views/client/SelectCust")
      }]
    },
    {
      path: "/check",
      name: "check",
      component: () => import("views/check/Check"),
      children: [{
        path: "selectCust",
        name: "selectCust",
        component: () => import("views/client/SelectCust")
      }]
    },
    {
      path: "/checklist",
      name: "checklist",
      component: () => import("views/check/CheckList"),
      children: [{
        path: "checkDetail",
        name: "checkDetail",
        component: () => import("views/check/CheckDetails")
      }]
    },
    {
      path: "/order",
      name: "order",
      component: newOrder,
      meta: {
        // keepAlive: true
      },
      children: [{
        path: "selectCust",
        name: "selectCust",
        component: () => import("views/client/SelectCust")
      },
      {
        path: "chooseProduct",
        name: "chooseProduct",
        component: () => import("components/order/chooseProduct")
      }, ]
    },
    {
      path: "/orderList",
      name: "orderList",
      component: () => import("views/order/OrderList"),
      meta: {
        keepAlive: true
      }
    },
    {
      path: "/orderInfo",
      name: "orderInfo",
      component: () => import("views/order/OrderInfo"),
      children: [{
        path: 'changeOrder',
        name: 'changeOrder',
        meta: {
          // keepAlive: true,
          refresh: false,
        },
        component: () => import('views/order/ChangeOrder'),
        children: [{
          path: "selectCust",
          name: "selectCust",
          component: () => import("views/client/SelectCust")
        },
        {
          path: "chooseProduct",
          name: "chooseProduct",
          component: () => import("components/order/chooseProduct")
        }, ]
      }]
    },
    {
      path: "/selectCustomer",
      name: "selectCustomer",
      component: () => import("components/client/select")
    },
    {
      path: "/selectLinkman",
      name: "selectLinkman",
      component: () => import("components/client/selectLinkman")
    },
    {
      path: "/chooseProduct",
      name: "chooseProduct",
      component: () => import("components/order/chooseProduct")
    },
    {
      path: "/cost",
      name: "cost",
      component: () => import("views/cost/costList"),
      children: [{
        path: "costInfo/:id",
        component: costInfo,
        props: true
      },
      {
        path: 'receiveInfo/:id',
        name: 'receiveInfo',
        component: () => import('views/receive/ReceiveInfo'),
        props: true,
      },
      {
        path: "newCost",
        component: newCost,
        children: [{
          path: "selectCust",
          component: () => import("views/client/SelectCust")
        }]
      }],
      // beforeRouteLeave (to, from, next) {
      //       if (from.path == '/Cost') {
      //       this.$router.push({path: '/'})
      //   }
      // }
    },
    {
      path: "/costInfo/:id",
      component: costInfo,
      props: true
    },
    {
      path: "/newCost",
      component: newCost,
      children: [{
        path: "selectCust",
        component: () => import("views/client/SelectCust")
      }]
    },
    {
      path: "/receive",
      name: "receive",
      component: () => import("views/receive/ReceiveList"),
      children: [

      ]
    },
    {
      path: "/addReceive",
      name: "addReceive",
      component: () => import("views/receive/AddReceive"),
      children: [{
        path: "selectOrder",
        name: "selectOrder",
        component: () => import("views/receive/orderSelector")
      },
      {
        path: "selectCust",
        name: "selectCustReceive",
        component: () => import("views/client/SelectCust")
      }]
    },
    {
      path: '/receiveInfo',
      name: 'receiveInfo',
      component: () => import('views/receive/ReceiveInfo'),
    },
    {
      path: '/changeReceive',
      name: 'changeReceive',
      meta: {
        refresh: false,
      },
      component: () => import('views/receive/AddReceive'),
      children: [{
        path: "selectCust",
        name: "selectCustChangeReceive",
        component: () => import("views/client/SelectCust")
      },
      {
        path: "selectOrder",
        name: "selectOrderChangeReceive",
        component: () => import("views/receive/orderSelector.vue")
      }]
    },
    {
      path: '/selectOrder',
      name: 'selectOrder',
      component: () => import('views/order/SelectOrder')
    },
    {
      path: "/TaskInfo",
      name: "TaskInfo",
      component: () => import("views/TaskInfo/template")
    },
    {
      path: "/attendance",
      name: "attendance",
      component: () => import('views/attendance/attendance')
    },
    {
      path: "/attendLocation",
      name: "attendLocation",
      component: () => import('views/attendance/location')
    },
    {
      path: "/statistics",
      name: "statistics",
      component: () => import('views/statistics/CheckStatic')
    },
    {
      path: "/statisIndex",
      name: "statisIndex",
      component: () => import('views/index/statisIndex')
    },
    {
      path: "/tipIndex",
      name: "tipIndex",
      component: () => import('views/index/tipIndex')
    },
    {
      path: "/birthdayClient",
      name: "birthdayClient",
      component: () => import('views/client/birthdayClient')
    },
    // 分类统计
    {
      path: "/classify",
      component: () => import('views/statistics/classify'),
      children: [{
        path: "statDetail",
        component: () => import('views/statistics/classify/detail')
      }]
    },
    // 时间统计
    {
      path: "/timeStat",
      component: () => import('views/statistics/timeStat'),
      children: [{
        path: "statDetail",
        component: () => import('views/statistics/classify/detail')
      }]
    },
    // 产品销量
    {
      path: "/productSavo",
      component: () => import('views/Product/Product'),
      meta: {
        keepAlive: true
      }
    },
    // 业绩排行
    {
      path: "/performance",
      component: () => import('views/Performance/Performance'),
      meta: {
        keepAlive: true
      }
    },
    {
      path: "/orderDetl",
      component: () => import('views/Performance/orderDetl/orderDetl')
    },
    {
      path: "/prodectDetails",
      component: () => import('views/Product/ProductDetails/ProductDetails')
    },
    // 业绩销量
    {
      path: "/perx",
      component: () => import('views/PerX/perx'),
      meta: {
        keepAlive: true
      },
      children: [{
        path: "typecollect",
        name:"typecollect",
        component: () => import("views/PerX/TypeCollect/typeCollect"),
        meta: {
          keepAlive: true
        }
      }]
    },
    //新建报销
    {
      path: "/reimbursement",
      component: () => import('views/cost/reimbursement'),
      children: [{
        path: "selectCust",
        component: () => import("views/client/SelectCust")
      }]
    },
    //客户报备
    {
      path: "/custrepor",
      component: () => import('views/CustRepor/CustRepor'),
      name: "custrepor",
      //  meta: {
      //    keepAlive: true
      //  },
       children: [{
         path: "addressMap",
         component: addressMap
       },{
         path: "selectCus",
         name: "selectCus",
           component: () => import('views/CustRepor/SelectCus/SelectCus')
       } ]
    }
  ]

});



