import axios from "axios";

export const initDict = ({ commit }, params) => {
  axios
    .get("/Common/GetPagedPubDictionaryList", {
      params: {
        dicType: params.typeID,
        page: 1,
        count: 100
      }
    })
    .then(res => {
      commit("setDictData", { type: params.typeID, data: res.data.Data.List });
      params.cb();
    });
};
export const initDictPromise = ({ commit }, type) => {
  return new Promise((resolve, reject) => {
    axios
      .get("/Common/GetPagedPubDictionaryList", {
        params: {
          dicType: type,
          page: 1,
          count: 100
        }
      })
      .then(res => {
        //新建费用收支时费用类型不允许选订单回款
        if (type == 22) {
          res.data.Data.List.forEach((item) => {
            if (item.CompID == 0) {
              res.data.Data.List.splice(item, 1)
            }
          })
        };
        commit("setDictData", {
          type: type,
          data: res.data.Data.List
        });
        resolve();
      });
  });
};
export const initAuth = ({ commit }) => {
  return new Promise((resolve, reject) => {
    axios.get(`${api50}/api/v1/YunModule/GetYunModuleAuthority?isMdt=0`).then(res => {
      let data = res.data.Data.find(item => item.name == "CRM")
      commit("setAuth", data ? data.children : [])
      resolve()
    })
  })

}
export const initIsAdmin = ({ commit, state }) => {
  return new Promise((resolve, reject) => {
    axios.get(`${mdcloud}yunos/auth/getAuthByUserFunc?userId=${state.CurrentUserID}`).then(res => {
      if (res.data.code == 200) {
        let data = res.data.data.data
        commit("setAdmin", data.isCompCreater || data.isAdmin)
        resolve()
      }
    })
  })
}



